<template>
  <div class="three-graph-container">
    <!-- Âä®ÊÄÅËÉåÊôØ -->
    <div class="animated-background">
      <div class="bg-layer layer1"></div>
      <div class="bg-layer layer2"></div>
      <div class="bg-layer layer3"></div>
    </div>
    
    <!-- ÊòüÁ©∫ËÉåÊôØ -->
    <div class="starry-background">
      <div v-for="n in 200" :key="n" class="star" :style="getStarStyle()"></div>
    </div>
    
    <!-- Âä†ËΩΩÂä®Áîª -->
    <div v-if="loading" class="loading-overlay">
      <div class="loading-spinner">
        <div class="spinner-ring"></div>
        <div class="loading-text">{{ loadingText }}</div>
      </div>
    </div>
    
    <!-- Ê†áÈ¢òÊ†è -->
    <div class="title-bar">
      <h1 class="main-title">
        <span class="title-icon">üåå</span>
        Áü•ËØÜÂõæË∞±
      </h1>
    </div>
    
    <!-- Three.js ÁîªÂ∏ÉÂÆπÂô® -->
    <div ref="threeContainer" class="three-container"></div>
    
    <!-- ÊéßÂà∂Èù¢Êùø -->
    <div class="control-panel">
      <div class="panel-header">
        <h3>üéÆ Three.js ÊéßÂà∂Âè∞</h3>
        <button @click="togglePanel" class="panel-toggle">
          {{ panelCollapsed ? 'Â±ïÂºÄ' : 'Êî∂Ëµ∑' }}
        </button>
      </div>
      
      <div class="panel-content" :class="{ collapsed: panelCollapsed }">
        <!-- Êï∞ÊçÆÊìç‰ΩúÂå∫ -->
        <div class="control-section">
          <h4>Êï∞ÊçÆÊìç‰Ωú</h4>
          <div class="control-buttons">
            <button @click="refreshData" class="btn btn-primary">
              üîÑ Âà∑Êñ∞Êï∞ÊçÆ
            </button>
          </div>
        </div>
        
        <!-- ËßÜÂõæÊéßÂà∂Âå∫ -->
        <div class="control-section">
          <h4>ËßÜÂõæÊéßÂà∂</h4>
          <div class="view-controls">
            <button @click="toggleAutoRotate" class="btn" :class="autoRotate ? 'btn-success' : 'btn-outline'">
              {{ autoRotate ? '‚è∏Ô∏è ÂÅúÊ≠¢Ëá™ËΩ¨' : '‚ñ∂Ô∏è ÂºÄÂßãËá™ËΩ¨' }}
            </button>
            <button @click="resetView" class="btn btn-outline">
              üéØ ÈáçÁΩÆËßÜËßí
            </button>
          </div>
        </div>
        
        <!-- ÁªüËÆ°‰ø°ÊÅØ -->
        <div class="control-section">
          <h4>ÁªüËÆ°‰ø°ÊÅØ</h4>
          <div class="info-cards">
            <div class="info-card">
              <div class="info-icon">üìä</div>
              <div class="info-content">
                <div class="info-value">{{ nodeCount }}</div>
                <div class="info-label">ËäÇÁÇπ</div>
              </div>
            </div>
            <div class="info-card">
              <div class="info-icon">üîó</div>
              <div class="info-content">
                <div class="info-value">{{ linkCount }}</div>
                <div class="info-label">ËøûÁ∫ø</div>
              </div>
            </div>
            <div class="info-card">
              <div class="info-icon">üéÆ</div>
              <div class="info-content">
                <div class="info-value">{{ Math.round(fps) }}</div>
                <div class="info-label">FPS</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- ËäÇÁÇπËØ¶ÊÉÖÂºπÁ™ó -->
    <div v-if="selectedNode" class="node-detail-modal" @click="closeNodeDetail">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>{{ selectedNode.name }}</h3>
          <button @click="closeNodeDetail" class="close-btn">√ó</button>
        </div>
        <div class="modal-body">
          <div class="node-info">
            <p><strong>Á±ªÂûã:</strong> {{ selectedNode.properties?.type || 'Êú™Áü•' }}</p>
            <p><strong>ÊùÉÈáç:</strong> {{ selectedNode.value }}</p>
            <p v-if="selectedNode.description"><strong>ÊèèËø∞:</strong> {{ selectedNode.description }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as THREE from 'three'
import { markRaw } from 'vue'
import apiService from '@/services/api'

export default {
  name: 'KnowledgeGraphThree',
  data() {
    return {
      // Êï∞ÊçÆÁõ∏ÂÖ≥
      graphData: { nodes: [], links: [] },
      nodeCount: 0,
      linkCount: 0,
      
      // ÊéßÂà∂ÂèÇÊï∞
      autoRotate: false,
      
      // UI Áä∂ÊÄÅ
      loading: false,
      loadingText: 'Âä†ËΩΩ‰∏≠...',
      panelCollapsed: false,
      selectedNode: null,
      fps: 60
    }
  },
  
  created() {
    // Three.js Áõ∏ÂÖ≥ÂØπË±°Â≠òÂÇ®Âú®ÁªÑ‰ª∂ÂÆû‰æã‰∏äÔºåÈÅøÂÖçÂìçÂ∫îÂºèÂåÖË£Ö
    this.scene = null
    this.camera = null
    this.renderer = null
    this.controls = null
    this.raycaster = null
    this.mouse = null
    this.nodeObjects = []
    this.linkObjects = []
  },
  
  mounted() {
    this.initThree()
    this.loadTestData()
    this.startAnimationLoop()
    this.startFPSCounter()
  },
  
  beforeUnmount() {
    this.cleanup()
  },
  
  methods: {
    // Three.js ÂàùÂßãÂåñ
    async initThree() {
      try {
        // ÂàõÂª∫Âú∫ÊôØ
        this.scene = markRaw(new THREE.Scene())
        this.scene.background = new THREE.Color(0x000011)
        
        // ÂàõÂª∫Áõ∏Êú∫
        const container = this.$refs.threeContainer
        this.camera = markRaw(new THREE.PerspectiveCamera(
          75,
          container.clientWidth / container.clientHeight,
          0.1,
          2000
        ))
        this.camera.position.set(100, 100, 100)
        
        // ÂàõÂª∫Ê∏≤ÊüìÂô®
        this.renderer = markRaw(new THREE.WebGLRenderer({ antialias: true }))
        this.renderer.setSize(container.clientWidth, container.clientHeight)
        this.renderer.setPixelRatio(window.devicePixelRatio)
        container.appendChild(this.renderer.domElement)
        
        // ÂàõÂª∫ÊéßÂà∂Âô®
        const { OrbitControls } = await import('three/examples/jsm/controls/OrbitControls.js')
        this.controls = markRaw(new OrbitControls(this.camera, this.renderer.domElement))
        this.controls.enableDamping = true
        this.controls.dampingFactor = 0.05
        this.controls.autoRotate = this.autoRotate
        
        // ÂàõÂª∫Â∞ÑÁ∫øÊäïÂ∞ÑÂô®
        this.raycaster = markRaw(new THREE.Raycaster())
        this.mouse = markRaw(new THREE.Vector2())
        
        // Ê∑ªÂä†ÁÅØÂÖâ
        this.addLights()
        
        // Ê∑ªÂä†‰∫ã‰ª∂ÁõëÂê¨
        this.addEventListeners()
        
        console.log('Three.js ÂàùÂßãÂåñÊàêÂäü')
      } catch (error) {
        console.error('Three.js ÂàùÂßãÂåñÂ§±Ë¥•:', error)
      }
    },
    
    // Ê∑ªÂä†ÁÅØÂÖâ
    addLights() {
      const ambientLight = markRaw(new THREE.AmbientLight(0x404040, 0.6))
      this.scene.add(ambientLight)
      
      const directionalLight = markRaw(new THREE.DirectionalLight(0xffffff, 0.8))
      directionalLight.position.set(100, 100, 50)
      this.scene.add(directionalLight)
      
      const pointLight = markRaw(new THREE.PointLight(0x4ecdc4, 1, 200))
      pointLight.position.set(50, 50, 50)
      this.scene.add(pointLight)
    },
    
    // Âä†ËΩΩÊµãËØïÊï∞ÊçÆ
    async loadTestData() {
      this.loading = true
      this.loadingText = 'Ê≠£Âú®ÁîüÊàêÊµãËØïÊï∞ÊçÆ...'
      
      try {
        const data = await apiService.generateSampleData()
        this.graphData = data
        this.nodeCount = data.nodes.length
        this.linkCount = data.links.length
        
        await this.renderGraph()
        this.loading = false
      } catch (error) {
        console.error('Âä†ËΩΩÊµãËØïÊï∞ÊçÆÂ§±Ë¥•:', error)
        this.loading = false
      }
    },
    
    // Ê∏≤ÊüìÂõæË∞±
    async renderGraph() {
      this.clearGraph()
      await this.renderNodes()
      await this.renderLinks()
      this.applyForceLayout()
    },
    
    // ÂàõÂª∫ÊñáÂ≠óË¥¥Âõæ
    createTextTexture(text, backgroundColor = 'transparent', textColor = '#ffffff') {
      const canvas = document.createElement('canvas')
      const context = canvas.getContext('2d')
      
      // ËÆæÁΩÆÁîªÂ∏ÉÂ§ßÂ∞è
      canvas.width = 512
      canvas.height = 256
      
      // ËÆæÁΩÆÂ≠ó‰Ωì - Â¢ûÂ§ßÂ≠óÂè∑
      context.font = 'Bold 48px Arial'
      
      // Â¶ÇÊûúÈúÄË¶ÅËÉåÊôØËâ≤ÔºåÁªòÂà∂ËÉåÊôØ
      if (backgroundColor !== 'transparent') {
        context.fillStyle = backgroundColor
        context.fillRect(0, 0, canvas.width, canvas.height)
      }
      
      // ÁªòÂà∂ÊñáÂ≠óÊèèËæπÔºàÂ¢ûÂä†ÂèØËØªÊÄßÔºâ
      context.strokeStyle = '#000000'
      context.lineWidth = 4
      context.textAlign = 'center'
      context.textBaseline = 'middle'
      
      // ÁªòÂà∂ÊñáÂ≠ó
      context.fillStyle = textColor
      
      // ÊñáÂ≠óÊç¢Ë°åÂ§ÑÁêÜ
      const maxWidth = canvas.width - 40
      const lines = this.wrapText(context, text, maxWidth)
      const lineHeight = 56
      const startY = (canvas.height - (lines.length - 1) * lineHeight) / 2
      
      lines.forEach((line, index) => {
        const y = startY + index * lineHeight
        // ÂÖàÁªòÂà∂ÊèèËæπ
        context.strokeText(line, canvas.width / 2, y)
        // ÂÜçÁªòÂà∂ÊñáÂ≠ó
        context.fillText(line, canvas.width / 2, y)
      })
      
      const texture = markRaw(new THREE.CanvasTexture(canvas))
      texture.needsUpdate = true
      return texture
    },
    
    // ÊñáÂ≠óÊç¢Ë°åÂ§ÑÁêÜ
    wrapText(context, text, maxWidth) {
      const words = text.split('')
      const lines = []
      let currentLine = ''
      
      for (const char of words) {
        const testLine = currentLine + char
        const metrics = context.measureText(testLine)
        
        if (metrics.width > maxWidth && currentLine !== '') {
          lines.push(currentLine)
          currentLine = char
        } else {
          currentLine = testLine
        }
      }
      lines.push(currentLine)
      return lines
    },
    
    // Ê∏≤ÊüìËäÇÁÇπ
    async renderNodes() {
      const nodeGeometry = markRaw(new THREE.SphereGeometry(1, 16, 16))
      
      for (let i = 0; i < this.graphData.nodes.length; i++) {
        const node = this.graphData.nodes[i]
        
        const color = new THREE.Color(this.getCategoryColor(node.category))
        const material = markRaw(new THREE.MeshPhongMaterial({
          color: color,
          transparent: true,
          opacity: 0.9
        }))
        
        const nodeMesh = markRaw(new THREE.Mesh(nodeGeometry, material))
        
        // ÈöèÊú∫ÂàùÂßã‰ΩçÁΩÆ
        nodeMesh.position.set(
          (Math.random() - 0.5) * 200,
          (Math.random() - 0.5) * 200,
          (Math.random() - 0.5) * 200
        )
        
        // ËÆæÁΩÆÂ§ßÂ∞è
        const size = Math.sqrt(node.value) * 0.3 + 1
        nodeMesh.scale.setScalar(size)
        
        // Â≠òÂÇ®ËäÇÁÇπÊï∞ÊçÆ
        nodeMesh.userData = node
        nodeMesh.userData.index = i
        
        this.scene.add(nodeMesh)
        this.nodeObjects.push(nodeMesh)
        
        // Ê∑ªÂä†ÊñáÂ≠óÊ†áÁ≠æ
        const textTexture = this.createTextTexture(node.name, 'transparent', '#ffffff')
        const textMaterial = markRaw(new THREE.MeshBasicMaterial({
          map: textTexture,
          transparent: true,
          alphaTest: 0.1,
          side: THREE.DoubleSide
        }))
        
        const textGeometry = markRaw(new THREE.PlaneGeometry(8, 4))
        const textMesh = markRaw(new THREE.Mesh(textGeometry, textMaterial))
        
        // Â∞ÜÊñáÂ≠óÊ†áÁ≠æÊîæÂú®ËäÇÁÇπ‰∏äÊñπ
        textMesh.position.copy(nodeMesh.position)
        textMesh.position.y += size + 3
        
        // Â≠òÂÇ®ÊñáÂ≠óÊ†áÁ≠æÊï∞ÊçÆÔºå‰æø‰∫éÂêéÁª≠Êõ¥Êñ∞
        textMesh.userData = { 
          nodeIndex: i, 
          isLabel: true,
          parentNode: nodeMesh
        }
        
        this.scene.add(textMesh)
        this.nodeObjects.push(textMesh)
      }
    },
    
    // Ê∏≤ÊüìËøûÁ∫ø
    renderLinks() {
      for (const link of this.graphData.links) {
        const sourceNode = this.nodeObjects.find(n => n.userData.id === link.source)
        const targetNode = this.nodeObjects.find(n => n.userData.id === link.target)
        
        if (sourceNode && targetNode) {
          const points = [sourceNode.position, targetNode.position]
          const geometry = markRaw(new THREE.BufferGeometry().setFromPoints(points))
          
          const color = new THREE.Color(this.getRelationshipColor(link.relationship))
          const material = markRaw(new THREE.LineBasicMaterial({
            color: color,
            transparent: true,
            opacity: 0.7
          }))
          
          const line = markRaw(new THREE.Line(geometry, material))
          line.userData = link
          
          this.scene.add(line)
          this.linkObjects.push(line)
        }
      }
    },
    
    // Â∫îÁî®ÂäõÂØºÂêëÂ∏ÉÂ±Ä
    applyForceLayout() {
      for (let iteration = 0; iteration < 50; iteration++) {
        // ÊéíÊñ•Âäõ
        for (let i = 0; i < this.nodeObjects.length; i++) {
          for (let j = i + 1; j < this.nodeObjects.length; j++) {
            const nodeA = this.nodeObjects[i]
            const nodeB = this.nodeObjects[j]
            
            const distance = nodeA.position.distanceTo(nodeB.position)
            if (distance < 50) {
              const force = 1000 / (distance * distance)
              const direction = markRaw(new THREE.Vector3())
                .subVectors(nodeA.position, nodeB.position)
                .normalize()
              
              nodeA.position.add(direction.multiplyScalar(force * 0.01))
              nodeB.position.sub(direction.multiplyScalar(force * 0.01))
            }
          }
        }
        
        // Âê∏ÂºïÂäõÔºàËøûÁ∫øÔºâ
        for (const link of this.linkObjects) {
          const sourceNode = this.nodeObjects.find(n => n.userData.id === link.userData.source)
          const targetNode = this.nodeObjects.find(n => n.userData.id === link.userData.target)
          
          if (sourceNode && targetNode) {
            const distance = sourceNode.position.distanceTo(targetNode.position)
            const idealDistance = 80
            const force = (distance - idealDistance) * 0.01
            
            const direction = markRaw(new THREE.Vector3())
              .subVectors(targetNode.position, sourceNode.position)
              .normalize()
            
            sourceNode.position.add(direction.multiplyScalar(force))
            targetNode.position.sub(direction.multiplyScalar(force))
          }
        }
      }
      
      this.updateLinkPositions()
    },
    
    // Êõ¥Êñ∞ËøûÁ∫ø‰ΩçÁΩÆ
    updateLinkPositions() {
      for (const link of this.linkObjects) {
        const sourceNode = this.nodeObjects.find(n => n.userData.id === link.userData.source)
        const targetNode = this.nodeObjects.find(n => n.userData.id === link.userData.target)
        
        if (sourceNode && targetNode) {
          const points = [sourceNode.position.clone(), targetNode.position.clone()]
          link.geometry.setFromPoints(points)
        }
      }
    },
    
    // Ê∏ÖÈô§ÂõæË∞±
    clearGraph() {
      for (const node of this.nodeObjects) {
        this.scene.remove(node)
      }
      this.nodeObjects = []
      
      for (const link of this.linkObjects) {
        this.scene.remove(link)
      }
      this.linkObjects = []
    },
    
    // Ê∑ªÂä†‰∫ã‰ª∂ÁõëÂê¨
    addEventListeners() {
      window.addEventListener('resize', this.onWindowResize)
      this.renderer.domElement.addEventListener('click', this.onMouseClick)
    },
    
    // Á™óÂè£Â§ßÂ∞èÂèòÂåñÂ§ÑÁêÜ
    onWindowResize() {
      const container = this.$refs.threeContainer
      this.camera.aspect = container.clientWidth / container.clientHeight
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(container.clientWidth, container.clientHeight)
    },
    
    // Èº†Ê†áÁÇπÂáªÂ§ÑÁêÜ
    onMouseClick(event) {
      this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1
      this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1
      
      this.raycaster.setFromCamera(this.mouse, this.camera)
      const intersects = this.raycaster.intersectObjects(this.nodeObjects)
      
      if (intersects.length > 0) {
        const clickedNode = intersects[0].object
        this.openNodeDetail(clickedNode.userData)
      }
    },
    
    // Âä®ÁîªÂæ™ÁéØ
    startAnimationLoop() {
      const animate = () => {
        requestAnimationFrame(animate)
        
        if (this.controls) {
          this.controls.update()
        }
        
        // ËäÇÁÇπÂä®Áîª
        for (const node of this.nodeObjects) {
          if (node.userData.isLabel) {
            // ÊñáÂ≠óÊ†áÁ≠æÂßãÁªàÊúùÂêëÊëÑÂÉèÊú∫
            node.lookAt(this.camera.position)
            // Êõ¥Êñ∞ÊñáÂ≠óÊ†áÁ≠æ‰ΩçÁΩÆË∑üÈöèÁà∂ËäÇÁÇπ
            if (node.userData.parentNode) {
              node.position.copy(node.userData.parentNode.position)
              node.position.y += node.userData.parentNode.scale.x + 3
            }
          } else {
            // ÊôÆÈÄöËäÇÁÇπÂä®Áîª
          node.position.y += Math.sin(Date.now() * 0.001 + node.userData.index) * 0.02
          node.rotation.y += 0.01
          }
        }
        
        this.updateLinkPositions()
        
        if (this.renderer && this.scene && this.camera) {
          this.renderer.render(this.scene, this.camera)
        }
      }
      
      animate()
    },
    
    // FPS ËÆ°Êï∞Âô®
    startFPSCounter() {
      let lastTime = performance.now()
      let frameCount = 0
      
      const updateFPS = () => {
        frameCount++
        const currentTime = performance.now()
        
        if (currentTime - lastTime >= 1000) {
          this.fps = frameCount
          frameCount = 0
          lastTime = currentTime
        }
        
        requestAnimationFrame(updateFPS)
      }
      
      updateFPS()
    },
    
    // Ëé∑ÂèñÂàÜÁ±ªÈ¢úËâ≤
    getCategoryColor(category) {
      const colors = ['#ff4500', '#ff1744', '#00e676', '#2979ff', '#e91e63', '#ffab00']
      return colors[category] || '#ffffff'
    },
    
    // Ëé∑ÂèñÂÖ≥Á≥ªÈ¢úËâ≤
    getRelationshipColor(relationship) {
      const colorMap = {
        'ÂåÖÂê´': '#4caf50',
        'ÂÖ≥ËÅî': '#2196f3',
        'Â∫îÁî®‰∫é': '#ff9800',
        'ÂÆûÁé∞': '#9c27b0',
        '‰ΩøÁî®': '#607d8b',
        'ËÆ≠ÁªÉ': '#f44336'
      }
      return colorMap[relationship] || '#4ecdc4'
    },
    
    // ÁîüÊàêÊòüÊòüÊ†∑Âºè
    getStarStyle() {
      return {
        left: Math.random() * 100 + '%',
        top: Math.random() * 100 + '%',
        animationDelay: Math.random() * 3 + 's'
      }
    },
    
    // ÊéßÂà∂Èù¢ÊùøÊñπÊ≥ï
    togglePanel() {
      this.panelCollapsed = !this.panelCollapsed
    },
    
    toggleAutoRotate() {
      this.autoRotate = !this.autoRotate
      if (this.controls) {
        this.controls.autoRotate = this.autoRotate
      }
    },
    
    resetView() {
      if (this.camera && this.controls) {
        this.camera.position.set(100, 100, 100)
        this.controls.reset()
      }
    },
    
    refreshData() {
      this.loadTestData()
    },
    

    
    openNodeDetail(node) {
      this.selectedNode = node
    },
    
    closeNodeDetail() {
      this.selectedNode = null
    },
    
    // Ê∏ÖÁêÜËµÑÊ∫ê
    cleanup() {
      window.removeEventListener('resize', this.onWindowResize)
      
      if (this.renderer) {
        this.renderer.dispose()
      }
      
      this.scene.traverse((object) => {
        if (object.geometry) {
          object.geometry.dispose()
        }
        if (object.material) {
          if (Array.isArray(object.material)) {
            object.material.forEach(material => material.dispose())
          } else {
            object.material.dispose()
          }
        }
      })
    }
  }
}
</script>

<style scoped>
.three-graph-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(135deg, #0c0c0c 0%, #1a1a2e 50%, #16213e 100%);
}

/* Âä®ÊÄÅËÉåÊôØ */
.animated-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.bg-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  animation: moveBackground 100s linear infinite;
}

.layer1 {
  background: radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%);
  animation-delay: -20s;
}

.layer2 {
  background: radial-gradient(circle at 60% 60%, rgba(70, 130, 180, 0.2) 0%, transparent 50%);
  animation-delay: -40s;
